@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: 'Amatic SC', cursive, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button, input {
  font-family: 'Amatic SC', cursive, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
-webkit-font-smoothing: antialiased;
-moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{display:grid;grid-template-areas:"header" "main" "footer";grid-template-rows:10% 1fr 10%;height:100vh}header{grid-area:header}main{grid-area:main;padding:2em}footer{grid-area:footer}

